"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import { Label } from "@/components/ui/label";

export function PermissionsForm() {
  const [permissions, setPermissions] = useState({
    canCreateUsers: false,
    canDeleteUsers: false,
    canEditSettings: false,
    canViewAnalytics: false,
    canManageDocuments: false,
  });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // TODO: Implement save functionality
    console.log({ permissions });
  };

  return (
    <Card>
      <CardHeader>
        <CardTitle>Permissions Settings</CardTitle>
        <CardDescription>
          Configure user permissions and access levels
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="space-y-4">
            <div className="flex items-center space-x-2">
              <Checkbox
                id="canCreateUsers"
                checked={permissions.canCreateUsers}
                onCheckedChange={(checked) =>
                  setPermissions({
                    ...permissions,
                    canCreateUsers: checked as boolean,
                  })
                }
              />
              <Label htmlFor="canCreateUsers">Can Create Users</Label>
            </div>

            <div className="flex items-center space-x-2">
              <Checkbox
                id="canDeleteUsers"
                checked={permissions.canDeleteUsers}
                onCheckedChange={(checked) =>
                  setPermissions({
                    ...permissions,
                    canDeleteUsers: checked as boolean,
                  })
                }
              />
              <Label htmlFor="canDeleteUsers">Can Delete Users</Label>
            </div>

            <div className="flex items-center space-x-2">
              <Checkbox
                id="canEditSettings"
                checked={permissions.canEditSettings}
                onCheckedChange={(checked) =>
                  setPermissions({
                    ...permissions,
                    canEditSettings: checked as boolean,
                  })
                }
              />
              <Label htmlFor="canEditSettings">Can Edit Settings</Label>
            </div>

            <div className="flex items-center space-x-2">
              <Checkbox
                id="canViewAnalytics"
                checked={permissions.canViewAnalytics}
                onCheckedChange={(checked) =>
                  setPermissions({
                    ...permissions,
                    canViewAnalytics: checked as boolean,
                  })
                }
              />
              <Label htmlFor="canViewAnalytics">Can View Analytics</Label>
            </div>

            <div className="flex items-center space-x-2">
              <Checkbox
                id="canManageDocuments"
                checked={permissions.canManageDocuments}
                onCheckedChange={(checked) =>
                  setPermissions({
                    ...permissions,
                    canManageDocuments: checked as boolean,
                  })
                }
              />
              <Label htmlFor="canManageDocuments">Can Manage Documents</Label>
            </div>
          </div>

          <Button type="submit">Save Permissions</Button>
        </form>
      </CardContent>
    </Card>
  );
}
